export default {
  name: 'activityFriendInfo',
  props: {
    scenicInfo: Object,
    assist: Object,
    inviteruser: Object
  },
  methods: {
    delHtmlTag (str) {
      if (str) {
        return str.replace(/<[^>]+>/g, '')
      } else {
        return ''
      }
    },
    helper () {
      this.$emit('helper')
    },
	makePhone () {
		uni.makePhoneCall({
			phoneNumber: this.scenicInfo.tel
		})
	},
    ativityRuleInfo () {
      this.$emit('ativityRuleInfo')
    }
  }
}
